import {useState} from 'react';
import {useDispatch} from 'react-redux';

import Container from '../../components/Container/Container';
import {register} from '../../redux/auth/auth-operations'
import styles from './RegisterView.module.css'

const RegisterView = ()=>{

    const [name, setName] = useState('');
    const [email,setEmail] = useState('');
    const [password, setPassword] = useState('');
    const dispatch = useDispatch()
    
    const handleSubmit = (e)=>{
        e.preventDefault()
        dispatch(register({name, email,password}));
        setName('');
        setEmail('');
        setPassword('');
    }
    return(
        <Container>
        <div className={styles.container}>
          <h2>Create an Account</h2>
          <form onSubmit={handleSubmit} className={styles.form}>
          <label htmlFor='Name'>
              Name
              <input value={name} onChange={(e)=> setName(e.target.value)} type="name" name="name" placeholder="Name" />
            </label>
            <label htmlFor='Name'>
              Email
              <input value={email} onChange={(e)=> setEmail(e.target.value)} type="email" name="email" placeholder="Email" />
            </label>
            <label htmlFor='Password'>
              Password
              <input value={password} onChange={(e)=> setPassword(e.target.value)} type="password" name="password" placeholder="Password" />
            </label>
            <button type="submit">Register</button>
          </form>
        </div>
      </Container>
    )
}
    
   


export default RegisterView